import { useState } from "react";
import {
  ActionFunction,
  Form,
  LoaderFunction,
  redirect,
  useActionData,
  useTransition,
} from "remix";
import { useLoaderData } from "remix";
import invariant from "tiny-invariant";

import { createPost, getPost, NewPost, Post } from "~/post";

type PostError = {
  title?: boolean;
  slug?: boolean;
  markdown?: boolean;
};

export const action: ActionFunction = async ({ request }) => {
  await new Promise((res) => setTimeout(res, 1000));
  const formData = await request.formData();

  const title = formData.get("title");
  const slug = formData.get("slug");
  const markdown = formData.get("markdown");

  const errors: PostError = {};
  if (!title) errors.title = true;
  if (!slug) errors.slug = true;
  if (!markdown) errors.markdown = true;

  if (Object.keys(errors).length) {
    return errors;
  }
  invariant(typeof title === "string");
  invariant(typeof slug === "string");
  invariant(typeof markdown === "string");
  await createPost({ title, slug, markdown });

  return redirect("/admin");
};

export const loader: LoaderFunction = async ({ params }) => {
  invariant(params.slug, "expected params.slug");
  return getPost(params.slug);
};

export default function EditSlug() {
  const errors = useActionData();
  const transition = useTransition();
  const [post, setPost] = useState<Post>(useLoaderData());
  return (
    <Form method="post">
      <p>
        <label>
          Post Title: {errors?.title ? <em>Title is required</em> : null}
          <input
            type="text"
            name="title"
            value={post.title}
            onChange={(e) => setPost({ ...post, title: e.currentTarget.value })}
          />
        </label>
      </p>
      <p>
        <label>
          Post Slug: {errors?.slug ? <em>Slug is required</em> : null}
          <input
            type="text"
            name="slug"
            value={post.slug}
            onChange={(e) => setPost({ ...post, slug: e.currentTarget.value })}
          />
        </label>
      </p>
      <p>
        <label htmlFor="markdown">Markdownd:</label>
        {errors?.markdown ? <em>Markdown is required</em> : null}
        <br />
        <textarea
          id="markdown"
          rows={20}
          name="markdown"
          value={post.body}
          onChange={(e) => setPost({ ...post, body: e.currentTarget.value })}
        />
      </p>
      <p>
        <button type="submit">
          {transition.submission ? "Creating..." : "Create Post"}
        </button>
      </p>
    </Form>
  );
}
